<template>
  <div>
    <div class='cart' v-if="islogin">
      <div class="wrap">
        <i class="iconfont icon-gouwuche large"></i>
        <p>您的购物车还是空的</p>
        <span>登录后即可同步您的购物车的商品</span>
        <mt-button type="primary" size="normal" @click='boun'>登录</mt-button>
      </div>
    </div>
    <div v-else>
      <CartItem
      v-for='item in cart'
      :key='item.id'
      :id='Number(item.id)'
      :count='item.count'
      :url='item.url'
      :title='item.title'
      :yunfei='item.yunfei'
      :yaofang='item.yaofang'
      :price='item.price'
      :changeCount='changeCartCountByIdAndType'
      ></CartItem>
    </div>
  </div>
</template>

<script>
import CartItem from '@/components/CartItem'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'cart',
  components: {
    CartItem
  },
  methods: {
    boun () {
      this.$router.history.push('/account')
    },
    ...mapMutations(['changeCartCountByIdAndType'])
  },
  computed: {
    ...mapState(['cart', 'islogin'])
  },
  mounted () {
    if (window.localStorage.getItem('userinfo') || this.cart.length > 1) {
      console.log('dsd')
      this.$store.state.islogin = false
    } else {
      this.$store.state.islogin = true
    }
  }
}
</script>

<style lang="scss" scoped>
.cart{
  display: flex;
  flex-direction: column;
  .wrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-around;
    p{
      font-size:20px;
      line-height:30px;
    }
    span{
      line-height: 30px;
    }
    .large{
      font-size:200px;
    }
    .mint-button{
      background: rgb(34, 162, 122);
    }
  }
}
</style>



