<template>
    <div class="business">
        <div class="listBusliness">
            <p class="Pharmacy">{{yaofang}}</p>
            <p class="title">{{title}}</p>
            <p class="fahuo">24小时发货</p>
            <div class="qian">
              <p class="HowMoney">${{price.toFixed(2)}}</p>
              <p class="yunfei">运费 {{yunfei}} 元</p>
            </div>
        </div>

        <div class="count">数量：
          <div class="unIncrease" @click="changeCount({type: 'unIncrease',id})">-</div>
          <div class="num"><input type="text" :value='count'></div>
          <div class="increase" @click="changeCount({type: 'Increase',id})">+</div>
        </div>
        <div class="add_cart">
          <mt-button type="primary">购买</mt-button>
          <mt-button type="danger">删除</mt-button>
        </div>
    </div>
</template>

<script>
export default {
  name: 'cart-item',
  props: {
    id: Number,
    title: String,
    url: String,
    count: Number,
    yunfei: Number,
    yaofang: String,
    price: Number,
    changeCount: Function
  }
}
</script>

<style lang="scss" scoped>
  .business{
    position: relative;
    background: #fff;
    border-top: 1px solid #999;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    line-height: 30px;
    .listBusliness {
      .Pharmacy {
        font-size: 16px;
        color: #000;
      }
      span {
        color: #999;
        i {
          font-style: normal;
          color:rgb(15, 179, 10);
        }
      }
      .fahuo {
        color:rgb(15, 179, 10);
        font-size: 14px;
      }
    }
    .qian {
      .HowMoney {
        color: rgba(219, 25, 25, 0.911);
        font-weight: 600;
      }
      .yunfei {
        font-size: 12px;
        color:rgb(70, 67, 67);
      }
    }
        .count {
      float: left;
      width: 127px;
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      .unIncrease, .increase{
        font-size: 20px;
        text-align: center;
        line-height: 20px;
        border:1px solid #999;
        float: left;
      }
      .unIncrease {
        width: 20px;
        height: 20px;
      }
      .increase {
        width: 20px;
        height: 20px;
      }
      .num {
        width: 30px;
        height: 20px;
        border:1px solid #999;
        border-left: 0;
        border-right: 0;
        float: left;
        input {
          width: 30px;
          border: none;
          line-height: 20px;
          text-align: center;
          display: block;
        }
      }
    }
  }
  .add_cart {
    position: absolute;
    top: 90px;
    right: 10px;
  }
</style>



